const pillStyle = {
  display: "flex",
  alignItems: "center",
  background: "#fff",
  borderRadius: 999,
  boxShadow: "0 2px 8px rgba(0,0,0,0.10)",
  padding: "0 14px 0 0",
  fontSize: 15,
  fontWeight: 600,
  border: "1.5px solid #e0e0e0",
  minHeight: 32,
  minWidth: 70,
  height: 32,
  lineHeight: '32px',
  marginLeft: 8,
  gap: 8,
  cursor: 'pointer',
};

const flagBoxStyle = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  background: '#fff',
  borderRadius: 8,
  border: '1.5px solid #e0e0e0',
  width: 32,
  height: 24,
  marginRight: 8,
  boxShadow: '0 1px 4px rgba(0,0,0,0.04)'
};

const countryTextStyle = {
  fontFamily: 'IBM Plex Sans, Arial, sans-serif',
  fontSize: 13,
  fontWeight: 600,
  color: '#222',
  letterSpacing: '0.01em',
  lineHeight: '24px',
  whiteSpace: 'nowrap',
  display: 'flex',
  alignItems: 'center',
};

const dotStyle = {
  fill: "#14532d",
  stroke: "#fff",
  strokeWidth: 2.5,
  filter: 'drop-shadow(0 1px 2px #bfa13a33)'
};

// Helper: Convert lat/lng to SVG x/y using equirectangular projection for SVG viewBox 0 0 2000 857
function latLngToXY(lat, lng, width = 2000, height = 857) {
  const x = ((lng + 180) * (width / 360));
  const y = ((90 - lat) * (height / 180));
  return { x, y };
}

export default function GlobalMap({ width = 1000, height = 500 }) {
  const [selectedCountry, setSelectedCountry] = useState(null);
  const [prayerTimes, setPrayerTimes] = useState(null);
  const [loading, setLoading] = useState(false);
  const [hoveredCountry, setHoveredCountry] = useState(null);
  const [selectedMethod, setSelectedMethod] = useState('2'); // Default: ISNA
  const [selectedSchool, setSelectedSchool] = useState('0'); // Default: Shafi
  const [sunX, setSunX] = useState(width * 0.2);
  const [animatePrayerTimes, setAnimatePrayerTimes] = useState(false);
  const isMobile = useIsMobile();

  // Animate sunlight position
  useEffect(() => {
    let frame;
    const animate = () => {
      setSunX(prev => {
        let next = prev + 0.5;
        if (next > width * 0.8) next = width * 0.2;
        return next;
      });
      frame = requestAnimationFrame(animate);
    };
    frame = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(frame);
  }, [width]);

  // Reset animation after it runs
  useEffect(() => {
    if (animatePrayerTimes) {
      const timeout = setTimeout(() => setAnimatePrayerTimes(false), 700);
      return () => clearTimeout(timeout);
    }
  }, [animatePrayerTimes]);

  const methodOptions = [
    { value: '2', label: 'ISNA' },
    { value: '1', label: 'University of Islamic Sciences, Karachi' },
    { value: '3', label: 'Muslim World League' },
    { value: '4', label: 'Umm Al-Qura, Makkah' },
    { value: '5', label: 'Egyptian General Authority' },
    { value: '7', label: 'Institute of Geophysics, University of Tehran' },
    // ...add more as needed
  ];
  const schoolOptions = [
    { value: '0', label: 'Shafi' },
    { value: '1', label: 'Hanafi' },
  ];

  const fetchPrayerTimes = async (country, method, school) => {
    setLoading(true);
    try {
      const params = { latitude: country.lat, longitude: country.lng, method, school };
      const data = await prayerTimesService.getTimingsByCoordinates(params);
      if (data && data.data && data.data.timings) {
        setPrayerTimes(data.data.timings);
        setAnimatePrayerTimes(true); // Trigger animation
      } else {
        setPrayerTimes({ error: 'Prayer times not available.' });
      }
    } catch (error) {
      setPrayerTimes({ error: 'Failed to fetch prayer times.' });
    }
    setLoading(false);
  };

  const handleCountryClick = async (country) => {
    setSelectedCountry(country);
    setSelectedMethod('2'); // Reset to default
    setSelectedSchool('0');
    await fetchPrayerTimes(country, '2', '0');
  };

  const handleMethodChange = async (e) => {
    const method = e.target.value;
    setSelectedMethod(method);
    if (selectedCountry) {
      await fetchPrayerTimes(selectedCountry, method, selectedSchool);
    }
  };

  const handleSchoolChange = async (e) => {
    const school = e.target.value;
    setSelectedSchool(school);
    if (selectedCountry) {
      await fetchPrayerTimes(selectedCountry, selectedMethod, school);
    }
  };

  const handleClose = () => {
    setSelectedCountry(null);
    setPrayerTimes(null);
  };

  // Marker style variables for easy adjustment
  const markerLineWidth = 0.3;
  const dotRadius = isMobile ? 2 : 3;
  const pillWidth = isMobile ? 90 : 128;
  const pillHeight = isMobile ? 24 : 32;
  const flagWidth = isMobile ? 18 : 28;
  const flagHeight = isMobile ? 13 : 20;

  return (
    <Card elevation={8} sx={{
      borderRadius: 6,
      overflow: 'visible',
      background: 'linear-gradient(135deg, #1976d2 0%, #26c6da 100%)', // theme blue to cyan
      p: 2,
      boxShadow: '0 8px 32px #0002'
    }}>
      <div style={{ width: "100%", maxWidth: width, margin: "0 auto", position: "relative", height }}>
        {/* Earth curve background with realistic gradient */}
        <svg width="100%" height={height} viewBox={`0 0 ${width} ${height}`} style={{ position: 'absolute', left: 0, top: 0, zIndex: 0 }}>
          <defs>
            <radialGradient id="earthGradient" cx="50%" cy="50%" r="80%">
              <stop offset="0%" stopColor="#e0f7fa" />
              <stop offset="60%" stopColor="#4dd0e1" />
              <stop offset="100%" stopColor="#01579b" />
            </radialGradient>
            <radialGradient id="sunlight" cx="0.5" cy="0.5" r="0.7">
              <stop offset="0%" stopColor="#fffde4" stopOpacity="0.7" />
              <stop offset="60%" stopColor="#fffde4" stopOpacity="0.2" />
              <stop offset="100%" stopColor="#fffde4" stopOpacity="0" />
            </radialGradient>
            <radialGradient id="movingSun" cx="0.5" cy="0.5" r="0.5">
              <stop offset="0%" stopColor="#fffde4" stopOpacity="0.7" />
              <stop offset="60%" stopColor="#ffe082" stopOpacity="0.25" />
              <stop offset="90%" stopColor="#000" stopOpacity="0.25" />
              <stop offset="100%" stopColor="#000" stopOpacity="0.35" />
            </radialGradient>
          </defs>
          {/* Full globe: centered circle filling the card */}
          <ellipse
            cx={width / 2}
            cy={height / 2}
            rx={Math.min(width, height) / 2}
            ry={Math.min(width, height) / 2}
            fill="url(#earthGradient)"
            style={{ filter: 'blur(2px)', opacity: 0.95 }}
          />
          {/* Sunlight overlay, centered */}
          <ellipse
            cx={width / 2}
            cy={height / 2}
            rx={Math.min(width, height) / 2}
            ry={Math.min(width, height) / 2}
            fill="url(#sunlight)"
            style={{ mixBlendMode: 'lighter', pointerEvents: 'none' }}
          />
          {/* Moving sun shadow/ellipse */}
          <ellipse
            cx={sunX}
            cy={height / 2}
            rx={width * 0.18}
            ry={height * 0.45}
            fill="url(#movingSun)"
            style={{ mixBlendMode: 'multiply', pointerEvents: 'none', opacity: 0.55, filter: 'blur(8px)' }}
          />
        </svg>
        <WorldSVG 
          style={{ 
            width: "100%", 
            height: "100%", 
            position: "absolute", 
            left: 0, 
            top: 0, 
            zIndex: 0, 
            pointerEvents: "none"
          }} 
        />
        <svg
          viewBox={`0 0 ${width} ${height}`}
          width="100%"
          height="100%"
          style={{ position: "absolute", left: 0, top: 0, zIndex: 1 }}
        >
          {/* Dynamic pill label positioning and marker lines */}
          {(() => {
            // Sort by x position
            const sortedCountries = [...countries].map(c => {
              const { x, y } = latLngToXY(c.lat, c.lng, width, height);
              return { ...c, x, y };
            }).sort((a, b) => a.x - b.x);

            // Track placed pill label bounding boxes (desktop only)
            const placedLabels = [];
            const defaultOffset = isMobile ? 0 : 40; // px above marker
            const offsetStep = isMobile ? 0 : 24; // px to stack higher if overlap

            return sortedCountries.map((c, idx) => {
              let pillX, pillY, markerLineX2, markerLineY2;
              let direction = 'right';
              if (isMobile) {
                direction = (c.x > width / 2) ? 'left' : 'right';
                pillY = -(pillHeight / 2);
                pillX = direction === 'right' ? 10 : -(pillWidth + 10);
                if (c.x + pillX < 0) pillX = -c.x;
                if (c.x + pillX + pillWidth > width) pillX = width - c.x - pillWidth;
                if (c.y + pillY < 0) pillY = -c.y;
                if (c.y + pillY + pillHeight > height) pillY = height - c.y - pillHeight;
                markerLineX2 = pillX + (direction === 'right' ? 0 : pillWidth);
                markerLineY2 = pillY + pillHeight / 2;
              } else {
                let offset = defaultOffset;
                let foundOverlap = true;
                while (foundOverlap) {
                  pillY = -(offset + dotRadius + pillHeight + 8);
                  pillX = -(pillWidth / 2);
                  const thisBox = {
                    x1: c.x + pillX,
                    x2: c.x + pillX + pillWidth,
                    y1: c.y + pillY,
                    y2: c.y + pillY + pillHeight
                  };
                  foundOverlap = placedLabels.some(box =>
                    !(thisBox.x2 < box.x1 || thisBox.x1 > box.x2 || thisBox.y2 < box.y1 || thisBox.y1 > box.y2)
                  );
                  if (foundOverlap) offset += offsetStep;
                }
                placedLabels.push({
                  x1: c.x + pillX,
                  x2: c.x + pillX + pillWidth,
                  y1: c.y + pillY,
                  y2: c.y + pillY + pillHeight
                });
                if (c.x + pillX < 0) pillX = -c.x;
                if (c.x + pillX + pillWidth > width) pillX = width - c.x - pillWidth;
                if (c.y + pillY < 0) pillY = -c.y;
                markerLineX2 = pillX + pillWidth / 2;
                markerLineY2 = pillY + pillHeight;
              }
              const adjustedX = c.x - 15;
              const adjustedY = c.y + 20;
              // SVG pill rendering for Safari compatibility
              return (
                <g key={c.code} transform={`translate(${adjustedX},${adjustedY})`} style={{ cursor: 'pointer' }}
                  onClick={() => handleCountryClick(c)}
                  onMouseEnter={() => setHoveredCountry(c.code)}
                  onMouseLeave={() => setHoveredCountry(null)}
                  tabIndex={0}
                  role="button"
                  aria-label={`Show prayer times for ${c.name}`}
                >
                  {/* Marker line */}
                  <line x1={0} y1={0} x2={markerLineX2} y2={markerLineY2} stroke="#bfa13a" strokeWidth={1.5} />
                  {/* Dot at the marker */}
                  <circle
                    r={dotRadius}
                    style={{
                      ...dotStyle,
                      transition: 'transform 0.2s, filter 0.2s',
                      transform: hoveredCountry === c.code ? 'scale(1.25)' : 'scale(1)',
                      filter: hoveredCountry === c.code ? 'drop-shadow(0 2px 6px #bfa13a66)' : dotStyle.filter,
                    }}
                  />
                  {/* SVG pill label */}
                  <g transform={`translate(${pillX},${pillY})`}>
                    <rect
                      width={pillWidth}
                      height={pillHeight}
                      rx={pillHeight / 2}
                      fill="#fff"
                      stroke="#e0e0e0"
                      strokeWidth={1.5}
                      filter={hoveredCountry === c.code ? 'drop-shadow(0 2px 8px #bfa13a33)' : 'drop-shadow(0 2px 8px rgba(0,0,0,0.10))'}
                    />
                    {/* Flag as SVG image */}
                    <image
                      href={`https://purecatamphetamine.github.io/country-flag-icons/3x2/${c.code}.svg`}
                      x={8}
                      y={(pillHeight - flagHeight) / 2}
                      width={flagWidth}
                      height={flagHeight}
                      style={{ borderRadius: 4 }}
                    />
                    {/* Country code text */}
                    <text
                      x={8 + flagWidth + (isMobile ? 5 : 10)}
                      y={pillHeight / 2 + (isMobile ? 4 : 6)}
                      fontFamily="IBM Plex Sans, Arial, sans-serif"
                      fontSize={isMobile ? 13 : 16}
                      fontWeight="600"
                      fill="#222"
                      letterSpacing="0.01em"
                      alignmentBaseline="middle"
                    >
                      {c.code}
                    </text>
                  </g>
                </g>
              );
            });
          })()}
        </svg>
        <Modal open={!!selectedCountry} onClose={handleClose}>
          <Box sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            bgcolor: 'background.paper',
            boxShadow: 24,
            p: 4,
            borderRadius: 2,
            minWidth: 340,
          }}>
            {selectedCountry && (
              <>
                <Typography variant="h6" gutterBottom>
                  {selectedCountry.name} ({selectedCountry.capital})
                </Typography>
                <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
                  <FormControl size="small" sx={{ minWidth: 120 }}>
                    <InputLabel id="method-label">Method</InputLabel>
                    <Select
                      labelId="method-label"
                      value={selectedMethod}
                      label="Method"
                      onChange={handleMethodChange}
                    >
                      {methodOptions.map(opt => (
                        <MenuItem key={opt.value} value={opt.value}>{opt.label}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  <FormControl size="small" sx={{ minWidth: 100 }}>
                    <InputLabel id="school-label">School</InputLabel>
                    <Select
                      labelId="school-label"
                      value={selectedSchool}
                      label="School"
                      onChange={handleSchoolChange}
                    >
                      {schoolOptions.map(opt => (
                        <MenuItem key={opt.value} value={opt.value}>{opt.label}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Box>
                {loading ? (
                  <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 120 }}>
                    <CircularProgress />
                  </Box>
                ) : prayerTimes ? (
                  prayerTimes.error ? (
                    <Typography color="error">{prayerTimes.error}</Typography>
                  ) : (
                    <motion.div
                      initial={false}
                      animate={animatePrayerTimes ? { scale: [1, 1.08, 1], boxShadow: ['0 0 0px #26c6da', '0 0 24px #26c6da88', '0 0 0px #26c6da'] } : {}}
                      transition={{ duration: 0.7, ease: 'easeInOut' }}
                    >
                      <Box>
                        {Object.entries(prayerTimes).map(([name, time]) => (
                          <Typography key={name}><b>{name}:</b> {time}</Typography>
                        ))}
                      </Box>
                    </motion.div>
                  )
                ) : null}
              </>
            )}
          </Box>
        </Modal>
      </div>
    </Card>
  );
}
